            {
              label: this.$t('TAC.myTodo.lastName'),
              key: 'lastName',
              prop: 'lastName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
          formData: this.delegateSearchFromData,
        };
      } else {
        return {
          formConfigList: this.delegateRoleSearchFormConfigList,
          formData: this.delegateRoleSearchFormData,
        };
      }
    },
  },
  created() {
    this.formData.creationTime = [
      dayjs(
        new Date(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 0, 0, 0).getTime() - 1000 * 3600 * 24 * 7)
      ).format('YYYY-MM-DD HH:mm:ss'),
      dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 23, 59, 59)).format('YYYY-MM-DD HH:mm:ss'),
    ];
    queryWorkFlowDataDict({
      dictCodes: [
        'CPS.WORKFLOW.CONFIGURATION.PRIORITY',
        'CPS.TASK.TYPE',
        'CPS.WORKFLOW.TODO.TASK.STATE',
        'WORK_FLOW_SORT_TIME',
      ],
    }).then((res) => {
      if (res.header && res.header.success) {
        this.searchFormPriority = res.dictDetailsMap['CPS.WORKFLOW.CONFIGURATION.PRIORITY'];
        if (this.searchFormPriority[0].label !== 'All') {
          this.searchFormPriority.unshift({ label: 'All', value: '' });
        }
        this.searchFormTaskType = res.dictDetailsMap['CPS.TASK.TYPE'];
        this.searchFormStatus = res.dictDetailsMap['CPS.WORKFLOW.TODO.TASK.STATE'];
        if (this.searchFormStatus[0].label !== 'All') {
          this.searchFormStatus.unshift({ label: 'All', value: '' });
        }
        this.todoInfoList.sortData = res.dictDetailsMap['WORK_FLOW_SORT_TIME'];
        this.sortTime = 'CREATE_TIME_LATEST';
      }
    });
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.searchTableLoading = true;
      vm.queryGroupTaskList();
    });
  },
  watch: {
    'processOptionTimeForm.customizedHours'(val) {
      if (val > 23) {
        this.processOptionTimeForm.customizedHours = 23;
      }
    },
    'processOptionTimeForm.customizedMinutes'(val) {
      if (val > 59) {
        this.processOptionTimeForm.customizedMinutes = 59;
      }
    },
    formData: {
      handler(val) {
        if (this.curSelectTaskNameToTaskType === val.taskType) return;
        this.formData.taskName = '';
      },
      deep: true,
    },
    selectProcessOptSingleCheck(val) {
      if (val) {
        this.selectProcessOptDialogTable.isSingleCheck = true;
      } else {
        this.selectProcessOptDialogTable.isSingleCheck = false;
      }
    },
    delegateSearchFromData: {
      handler(val) {
        if (val.operatorType === 'orgOperator') {
          if (this.processOptSearchFrom.formConfigList.length === 5) {
            this.newSearchType = true;
            this.processOptSearchFrom.formConfigList.splice(1, 0, this.shortCodeConfig);
          }
        } else {
          if (this.processOptSearchFrom.formConfigList.length === 6) {
            this.newSearchType = false;
            this.processOptSearchFrom.formConfigList.splice(1, 1);
          }
        }
        if (val.shortCode !== '') {
          this.$refs.selectProcessOptDialogRef.$refs.dialogSearchForm.clearValidate('shortCode');
        }
      },
      deep: true,
    },
    newSearchType(val) {
      if (val) {
        this.selectProcessOptDialogTable.getTableData = this.getOrgDelegateDialogData;
      } else {
        this.selectProcessOptDialogTable.getTableData = this.getDelegateDialogData;
      }
    },
  },
  methods: {
    customizedDayValidator(rule, val, callback) {
      let value = this.processOptionTimeForm.customizedDay * 1;
      if (value <= 100 && value >= 0) {
        callback();
      } else {
        this.$refs.processOptForm.clearValidate('customizedMinutes');
        this.$refs.processOptForm.clearValidate('customizedHours');
        callback(new Error());
      }
    },
    customizedHoursValidator(rule, val, callback) {
      let value = this.processOptionTimeForm.customizedHours * 1;
      if (value <= 23 && value >= 0) {
        callback();
      } else {
        this.$refs.processOptForm.clearValidate('customizedMinutes');
        this.$refs.processOptForm.clearValidate('customizedDay');
        callback(new Error());
      }
    },
    customizedMinutesValidator(rule, val, callback) {
      let value = this.processOptionTimeForm.customizedMinutes * 1;
      if (value <= 59 && value >= 0) {
        callback();
      } else {
        this.$refs.processOptForm.clearValidate('customizedHours');
        this.$refs.processOptForm.clearValidate('customizedDay');
        callback(new Error());
      }
    },
    getFutureTime() {
      const DayM = this.processOptionTimeForm.customizedDay;
      const HoursM = this.processOptionTimeForm.customizedHours;
      const MinutesM = this.processOptionTimeForm.customizedMinutes;

      const DayMres = DayM ? Number(DayM) : 0;
      const HoursMres = HoursM ? Number(HoursM) : 0;
      const MinutesMres = MinutesM ? Number(MinutesM) : 0;

      const Minutes = DayMres * 24 * 60 + HoursMres * 60 + MinutesMres;

      const nowData = new Date(); // 1.
      const nowMinutes = nowData.getMinutes(); // 2. 
      nowData.setMinutes(nowMinutes + Minutes); // 3. +15+15date
      return nowData.toLocaleString().replace(/\//g, '-');
    },
    selectOperatorListValidator(rule, val, callback) {
      if (!this.selectOperatorList.length) {
        this.isCheckError = true;
        callback(new Error());
        return;
      } else {
        this.isCheckError = false;
      }
      callback();
    },
    handleProcessCancelClick() {
      if (this.tempTaskHandle.length > 0) {
        this.checkLists = this.tempTaskHandle.splice(0);
      } else {
        this.checkLists.splice(0);
      }
      this.$refs.processOptForm.resetFields();
      this.isCheckError = false;
      this.processOperationDialogVisible = false;
      this.processOperationFormData.operation = 'approve';
      this.processOperationFormData.remark = '';
      this.processOperationFormData.investigateOperator = '';
      this.processOperationFormData.validityPeriod = '';
      this.processOperationFormData.initialSubmit = false;
    },
    handleProcessConfirmClick() {
      this.$refs.processOptForm.validate((result) => {
        if (result) {
          this.isCheckError = false;
          let processList = this.checkLists.map((item) => {
            return {
              processId: item.processId,
              taskId: item.taskId,
            };
          });
          let comments = this.processOperationFormData.remark;
          let isBatch = false;
          let actionType = this.processOperationFormData.operation;
          let assigneeType = this.assigneeType == 'role' ? '3' : '1';
          let assignee = [];
          const date =
            60 * 24 * this.processOptionTimeForm.customizedDay +
            60 * this.processOptionTimeForm.customizedHours +
            1 * this.processOptionTimeForm.customizedMinutes;
          if (this.delegateOperatorList.length > 0) {
            assignee = this.delegateOperatorList.map((item) => {
              if (item.roleId) {
                return { roleId: item.roleId };
              }
              return { operatorId: item.identityId, operatorType: assigneeType };
            });
          }
          if (this.selectOperatorList.length > 0) {
            assignee = this.selectOperatorList.map((item) => {
              if (item.roleId) {
                return { roleId: item.roleId };
              }
              return { operatorId: item.identityId, operatorType: assigneeType };
            });
          }
          let params = {};
          if (actionType === 'approve' || actionType === 'complete') {
            params = {
              actionType,
              comments,
              processList,
              isBatch,
            };
          }
          if (actionType === 'reject') {
            if (this.processOperationFormData.initialSubmit) {
              actionType = 'backinitiator';
            }
            params = {
              actionType,
              comments,
              processList,
              isBatch,
            };
          }
          if (actionType === 'investigate') {
            if (this.processOperationFormData.investigateOperator === 'initiator') {
              if (this.processOperationFormData.validityPeriod === 'sameAsOriginal') {
                params = {
                  processList,
                  isBatch,
                  actionType,
                  comments,
                  isResetExpireTime: false,
                };
              }
              if (this.processOperationFormData.validityPeriod === 'customized') {
                params = {
                  processList,
                  isBatch,
                  actionType,
                  comments,
                  taskExpireTimeInterval: date,
                  isResetExpireTime: true,
                };
              }
            }
            if (this.processOperationFormData.investigateOperator === 'others') {
              if (this.processOperationFormData.validityPeriod === 'sameAsOriginal') {
                params = {
                  processList,
                  isBatch,
                  assigneeType,
                  actionType,
                  comments,
                  assignee,
                  isResetExpireTime: false,
                };
              }
              if (this.processOperationFormData.validityPeriod === 'customized') {
                params = {
                  processList,
                  isBatch,
                  assigneeType,
                  actionType,
                  assignee,
                  comments,
                  taskExpireTimeInterval: date,
                  isResetExpireTime: true,
                };
              }
            }
          }
          batchProcess(params).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess('Operation succeeded.');
              this.checkLists = [];
              this.$refs.todoinfo.reset();
              this.delegateOperatorList.splice(0);
              this.selectOperatorList.splice(0);
              this.$nextTick(() => {
                this.queryGroupTaskList();
              });
            }
          });
          this.processOperationDialogVisible = false;
        }
      });
    },
    selectProcessOptCloseDialog() {
      this.$refs.selectProcessOptDialogRef.$refs.dialogSearchForm.resetFields();
      this.selectProcessOptDialogVisible = false;
    },
    selectProcessOptDialogReset() {
      this.$refs.selectProcessOptDialogRef.$refs.dialogSearchForm.resetFields();
      Object.keys(this.$refs.selectProcessOptDialogRef.$refs.dialogSearchForm.formData).forEach((key) => {
        this.$refs.selectProcessOptDialogRef.$refs.dialogSearchForm.formData[key] = '';
      });
      this.newSearchType = false;
      this.delegateSearchFromData.operatorType = 'spOperator';
      this.$nextTick(() => {
        this.delegateRoleSearchFormData.roleName = '';
        this.selectProcessOptDialogTable.isSearch = true;
      });
    },
    comfirmselectProcessOptHandler() {
      if (this.selectProcessOptSingleCheck) {
        if (Object.keys(this.tempSingleCheck).length === 0) {
          this.$modal.msgError('Please select at least one.');
          return;
        }
        this.delegateOperatorList.splice(0);
        this.delegateOperatorList.push(this.tempSingleCheck);
        this.$refs.processOptForm.validateField('selectOperatorList', () => {});
        this.selectProcessOptCloseDialog();
      } else {
        if (this.tempMultiCheck.length === 0) {
          this.$modal.msgError('Please select at least one.');
          return;
        }
        this.selectOperatorList = this.tempMultiCheck;
        this.$refs.processOptForm.validateField('selectOperatorList', () => {});
        this.selectProcessOptCloseDialog();
      }
    },
    processCheckChange(val) {
      if (val === 'role') {
        this.tempDelegateTableCol = JSON.parse(JSON.stringify(this.selectProcessOptDialogTable.columns));
        this.selectProcessOptDialogTable.columns = [
          {
            label: this.$t('TAC.myTodo.role'),
            prop: 'roleName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.description'),
            prop: 'description',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.roleCategory'),
            prop: 'roleCategory',
            align: 'left',
          },
        ];
        this.selectProcessOptDialogTable.getTableData = this.qeuryRoleData;
        this.$nextTick(() => {
          this.selectProcessOptDialogTable.isSearch = true;
        });
      } else {
        this.selectProcessOptDialogTable.columns = this.tempDelegateTableCol;
        this.selectProcessOptDialogTable.getTableData = this.getDelegateDialogData;
        this.selectProcessOptDialogReset();
        this.processOptSearchFrom.formData.operatorType = 'spOperator';
        this.$nextTick(() => {
          this.selectProcessOptDialogTable.isSearch = true;
        });
      }
    },
    singleCheckChange(val) {
      this.tempSingleCheck = val;
    },
    multiCheckChange(val) {
      this.tempMultiCheck = val;
    },
    getDelegateDialogData() {
      return new Promise((resolve) => {
        let params = {
          operatorType: 20,
          status: '03',
          userName: this.delegateSearchFromData.userName,
          firstName: this.delegateSearchFromData.firstName,
          middleName: this.delegateSearchFromData.middleName,
          lastName: this.delegateSearchFromData.lastName,
          pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
          pageSize: this.delegateDialogTable.paginationConfig.pageSize,
        };
        queryCurrentHandler(params).then((res) => {
          this.selectProcessOptSearchLoading = false;
          this.selectProcessOptDialogTable.isSearch = false;
          if (res.header && res.header.success) {
            res.list.forEach((item, idx) => {
              res.list[idx].id = item.identityId;
            });
            res.totalRecords = res.total;
            return resolve(res);
          }
        });
      });
    },
    async getOrgDelegateDialogData() {
      let params = {
        shortCode: this.delegateSearchFromData.shortCode,
        operatorName: this.delegateSearchFromData.userName,
        selectType: 'operator',
        status: '03',
        operatorType: 30,
        pageSize: this.delegateDialogTable.paginationConfig.pageSize,
        pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
      };
      let res = await queryOrgCurrentHandler(params);
      this.selectProcessOptSearchLoading = false;
      this.selectProcessOptDialogTable.isSearch = false;
      if (res.header && res.header.success) {
        let list = res.list.map((item) => {
          // item.userName = item.operatorName;
          item.id = item.operatorId;
          return item;
        });
        return {
          header: res.header,
          list,
          totalRecords: res.totalSize,
        };
      }
    },
    removeSelected(record) {
      this.delegateOperatorList.splice(this.delegateOperatorList.indexOf(record), 1);
    },
    openSingleCheckDilog() {
      this.selectProcessOptSingleCheck = true;
      this.selectProcessOptDialogVisible = true;
    },
    openMultiCheckDilog() {
      this.selectProcessOptSingleCheck = false;
      this.processOptSearchFrom.formData.operatorType = 'spOperator';
      this.selectProcessOptDialogVisible = true;
    },
    removeSelecteOperator(record) {
      this.selectOperatorList.splice(this.selectOperatorList.indexOf(record), 1);
      if (this.selectOperatorList.length) {
        this.$refs.processOptForm.validateField('selectOperatorList', () => {});
      }
    },
    qeuryRoleData() {
      let params = {
        selectType: 'role',
        roleName: this.delegateRoleSearchFormData.roleName,
        roleCategory: this.delegateRoleSearchFormData.roleCategory,
        pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
        pageSize: this.delegateDialogTable.paginationConfig.pageSize,
      };
      return new Promise((resolve) => {
        queryRole(params).then((res) => {
          this.selectProcessOptSearchLoading = false;
          this.delegateDialogTable.isSearch = false;
          this.selectProcessOptDialogTable.isSearch = false;
          if (res.header && res.header.success) {
            let tempData = res.list.map((item) => {
              item.id = item.roleId;
              switch (item.roleType) {
                case '2000':
                  item.roleCategory = 'Sp Operator';
                  break;
                case '3000':
                  item.roleCategory = 'Organization Operator';
                  break;
                case '5000':
                  item.roleCategory = 'Organization';
                  break;
                case '7000':
                  item.roleCategory = 'GROUP';
                  break;
                case '8000':
                  item.roleCategory = 'SP';
                  break;
                default:
                  break;
              }
              return item;
            });
            if (this.delegateRoleSearchFormData.roleCategory) {
              tempData = tempData.filter((item) =>
                item.roleType.includes(this.delegateRoleSearchFormData.roleCategory)
              );
            }
            resolve({
              header: res.header,
              list: tempData.slice(
                (this.delegateDialogTable.paginationConfig.pageIndex - 1) *
                  this.delegateDialogTable.paginationConfig.pageSize,
                this.delegateDialogTable.paginationConfig.pageSize * this.delegateDialogTable.paginationConfig.pageIndex
              ),
              totalRecords: tempData.length,
            });
          }
        });
      });
    },
    resetSearch() {
      this.$refs.searchFormRef.resetFields();
      this.queryGroupTaskList();
    },
    queryGroupTaskList(pageNo = 1, pageSize = 10) {
      this.searchTableLoading = true;
      let tskType = '';
      if (this.formData.taskType.length > 1) {
        this.searchFormTaskType.some((item) => {
          if (item.label === this.formData.taskType) {
            tskType = item.value;
            return true;
          }
        });
      } else {
        tskType = this.formData.taskType;
      }
      let params = {
        taskId: this.formData.taskNo || '',
        taskName: this.formData.taskName || '',
        priority: this.formData.priority || null,
        category: tskType,
        status: this.formData.status || null,
        dueTime: this.formData.dueTime || null,
        pageNo,
        createTimeFrom: this.formData.creationTime[0],
        createTimeTo: this.formData.creationTime[1],
        sortingMode: this.sortTime || 'CREATE_TIME_LATEST',
        pageSize,
      };
      queryGroupTask({ taskVO: params }).then((res) => {
        this.searchTableLoading = false;
        this.searchLoading = false;
        if (res.header && res.header.success) {
          this.todoInfoList.count = res.count;
          this.todoInfoList.todoInfoLists.splice(0);
          res.taskDTOList.forEach((item) => {
            let time = item.createTime.split(' ');
            let date = time[0].split('-');
            let createTime = date[2] + '/' + date[1] + '/' + date[0] + ' ' + time[1];
            let eTime = item.expireTime.split(' ');
            let expDate = eTime[0].split('-');
            let expireTime = expDate[2] + '/' + expDate[1] + '/' + expDate[0] + ' ' + eTime[1];
            item.title = item.taskId + '-' + item.taskName;
            item.createTime = createTime;
            item.expireTime = expireTime;
            this.todoInfoList.todoInfoLists.push(item);
          });
        }
      });
    },
    selectTaskDialogSearch() {
      this.dialogTable.isSearch = true;
      this.dialogTaskNameFormBtnLoading = true;
    },
    handleSortConditions(val) {
      this.sortTime = val;
      this.queryGroupTaskList();
    },
    handleSizeChange(val) {
      let pageNo = val.pageIndex;
      let pageSize = val.pageSize;
      this.pageSize = val.pageSize;
      this.queryGroupTaskList(pageNo, pageSize);
    },
    search() {
      this.$refs.searchFormRef.validate((result) => {
        if (result) {
          this.searchLoading = true;
          this.searchTableLoading = true;
          this.queryGroupTaskList();
        }
      });
    },
    taskNameDialigSelectChange(val) {
      this.tempTaskName = val;
    },
    async getTaskNameDialogData() {
      let taskTypeCondition = '';
      let taskTypeCodetoLabel = '';
      this.searchFormTaskType.some((item) => {
        if (item.value === this.taskNameSearchFormData.taskType) {
          taskTypeCodetoLabel = item.label;
          return true;
        }
      });
      if (taskTypeCodetoLabel) {
        if (taskTypeCodetoLabel !== 'All') {
          if (taskTypeCodetoLabel === 'Transaction and Action') {
            taskTypeCondition = 'TRANSATION_AND_ACTION';
          } else {
            taskTypeCondition = taskTypeCodetoLabel.toUpperCase().split(' ').join('_');
          }
        }
      }
      let params = {
        taskNameCondition: this.taskNameSearchFormData.taskName,
        taskTypeCondition,
      };
      let res = await queryTaskName(params);
      this.dialogTaskNameFormBtnLoading = false;
      this.dialogTable.isSearch = false;
      if (res.taskTypeList && res.taskTypeList.length > 0) {
        this.taskNameCount = res.taskTypeList.length;
        this.taskNameHasData = false;
        let list = res.taskTypeList.map((item) => {
          item.taskType = item.category;
          return item;
        });
        this.taskNameDialogList = list;
        return {
          header: { success: true },
          list: list.slice(0, this.taskNamePageSize),
        };
      }
    },
    comfirmTaskNameHandler() {
      if (!this.tempTaskName || !Object.keys(this.tempTaskName).length) {
        this.$modal.msgError('Please select at least one.');
        return;
      }
      this.formData.taskName = this.tempTaskName.taskName;
      if (this.tempTaskName.taskType === 'TRANSATION_AND_ACTION') {
        this.formData.taskType = 'Transaction and Action';
        this.curSelectTaskNameToTaskType = 'Transaction and Action';
      } else {
        if (this.tempTaskName.taskType) {
          let taskType = this.tempTaskName.taskType
            .toLocaleLowerCase()
            .split('_')
            .map((item) => {
              return item.replace(item[0], item[0].toLocaleUpperCase());
            });
          this.formData.taskType = taskType.join(' ');
          this.curSelectTaskNameToTaskType = taskType.join(' ');
        }
      }
      this.$refs.dialogTaskNameForm.resetFields();
      this.tempTaskName = null;
      this.taskNameDialogVisible = false;
    },
    checkTaskChange(val) {
      if (val.isCheck) {
        this.checkLists.push(val);
      } else {
        let index = this.checkLists.findIndex((item) => {
          return item.taskId === val.taskId;
        });
        this.checkLists.splice(index, 1);
      }
    },
    checkProcess(val) {
      return val.taskState !== 'Paused' && val.processFlag;
    },
    processing(handleData) {
      if (handleData) {
        if (this.checkLists.length > 0) {
          this.tempTaskHandle = this.checkLists.splice(0);
        }
        this.currentData = handleData;
        this.checkLists.push(handleData);
        this.processOperationDialogVisible = true;
      } else if (this.checkLists.length > 0) {
        let checkResult = this.checkLists.map((item) => {
          return this.checkProcess(item);
        });
        if (checkResult.includes(false)) {
          this.$modal.alertWarning('Operation failed because the selected records contain records cannot be resolved');
          return;
        }
        if (this.checkLists.length > 1) {
          let isSameTypeList = [];
          this.checkLists.reduce((pre, cur) => {
            if (pre.taskType === cur.taskType) {
              isSameTypeList.push(true);
            } else {
              isSameTypeList.push(false);
            }
            return cur;
          });
          if (isSameTypeList.includes(false)) {
            this.notificationDialogVisible = true;
            return;
          }
        }
        this.taskProcessDialogVisible = true;
      } else {
        this.$modal.alertWarning('Select at least one record.');
      }
    },
    closeDialog() {
      this.$refs.dialogTaskNameForm.resetFields();
      this.tempTaskName = null;
      this.dialogTable.isSearch = false;
      this.taskNameDialogVisible = false;
    },
    selectTaskDialogReset() {
      this.$refs.dialogTaskNameForm.resetFields();
      this.dialogTable.isSearch = true;
    },
    handleCancelClick() {
      this.$refs.dialogProcessForm.resetFields();
      this.taskProcessDialogVisible = false;
      this.tempTaskHandle.forEach((item) => {
        this.checkLists.push(item);
      });
    },
    handleConfirmClick() {
      this.$refs.dialogProcessForm.validate((result) => {
        if (result) {
          this.$modal
            .confirm('', 'Are you sure to submit?', {
              cancelButtonText: this.$t('common.button.cancel'),
              confirmButtonText: this.$t('common.button.confirm'),
            })
            .then(
              (res) => {
                if (res === 'confirm') {
                  let isBatch = this.checkLists.length > 1 ? true : false;
                  let processList = this.checkLists.map((item) => {
                    return {
                      processId: item.processId,
                      taskId: item.taskId,
                    };
                  });
                  let actionType = this.taskProcessFormData.operation;
                  if (actionType === 'reject') {
                    if (this.initialSubmit) {
                      actionType = 'backinitiator';
                    }
                  }
                  batchProcess({
                    actionType,
                    comments: this.taskProcessFormData.remark,
                    isBatch,
                    processList,
                  }).then((res) => {
                    if (res.header && res.header.success) {
                      this.checkLists.splice(0);
                      this.$modal.msgSuccess('Operation succeeded.');
                      this.taskProcessDialogVisible = false;
                      this.queryMyToDoTaskLists();
                    }
                  });
                }
              },
              () => {}
            );
        }
      });
    },
    handleTaskNameSizeChange(val) {
      let pageNo = val.pageIndex;
      let pageSize = val.pageSize;
      this.taskNamePageSize = val.pageSize;
      this.$refs.taskNameTableRef.tableData = this.taskNameDialogList.slice(
        (pageNo - 1) * this.taskNamePageSize,
        (pageNo - 1) * this.taskNamePageSize + pageSize
      );
    },
  },
};
</script>

